#bodyEdit {

    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    background-position: center;
    /* Center the image */


}

#divEdit {
    background-color: rgba(255, 255, 255, 0.5);
    padding-top: 60px;
    padding: 30px;
    height: 900px;
    margin-left: 60px;
    margin-right: 60px;
    border-radius: 40px;

}

.badgeAdmin {
    font-family: VisbyRound;
    text-align: center;
    margin-bottom: 0.5em;
}

#formEdit {
    background-color: rgba(255, 255, 255, 0.5);
    margin: 50px;
    padding-top: 60px;
    margin-right: 135px;
    padding: 200px;
    align-items: center;

}

.label {
    padding: 10px;
    font-size: 20px;
}